




































import { Component, Vue } from 'vue-property-decorator';
import Uibtn from '@/components/Uibtn/Uibtn.vue'

@Component({
    components: {
        Uibtn
    },
})

export default class Home extends Vue {
    //属性设置，初始化
    private xsmall:boolean=false 
    private small:boolean=false 
    private large:boolean=false 
    private xlarge:boolean=false 
    private tile:boolean=false 
    private rounded:boolean=false 
    private circle:boolean=false 
    private disabled:boolean=false
    //禁用方法
    private changeDisabled() {
        this.disabled=!this.disabled
    }
    //改变按钮边框弧度方法
    private changeRadius(name:String) {
        switch (name) {
            case 'tile':
                this.tile=true
                this.rounded=false
                this.circle=false
                break;
            case 'normal':
                this.tile=false
                this.rounded=false
                this.circle=false
                break;
            case 'rounded':
                this.tile=false
                this.rounded=true
                this.circle=false
                break;
            case 'circle':
                this.tile=false
                this.rounded=false
                this.circle=true
                break;
        }
    }
    //改变按钮大小方法
    private resize(name:String){
        switch (name) {
            case 'xsmall':
                this.xsmall=true
                this.small=false
                this.large=false
                this.xlarge=false
                break;
            case 'small':
                this.xsmall=false
                this.small=true
                this.large=false
                this.xlarge=false
                break;
            case 'normal':
                this.xsmall=false
                this.small=false
                this.large=false
                this.xlarge=false
                break;
            case 'large':
                this.xsmall=false
                this.small=false
                this.large=true
                this.xlarge=false
                break;
            case 'xlarge':
                this.xsmall=false
                this.small=false
                this.large=false
                this.xlarge=true
                break;
        }
    }
    //按钮监听
    private onClick(event:MouseEvent){
    //    console.log(event);
   }
}

